<template>
  <v-list-item :key="item.repository.title" class="repo-container">
    <v-list-item-content>
      <div class="row">
        <div class="col">
          <h3 class="repo-title">
            {{ item.repository.full_name }}
          </h3>
          <p class="repo-desc">{{ item.repository.description }}</p>
          <p class="repo-info">{{ item.repository.language }}</p>
        </div>
        <div class="col-3" style="align-self: center; text-align: end">
          <div v-for="tag in Object.keys(item.roles)" v-bind:key="tag">
            <v-btn color="#272727" class="ma-2 white--text" v-if="item.roles[tag]" elevation="6" @click="saveRole(tag, item.repository.full_name)">{{ tag }}</v-btn>
          </div>
        </div>
      </div>
    </v-list-item-content>
  </v-list-item>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "RepositoryItem",
  props: {
    item: {
      type: Object,
      default: function () {
        return null;
      },
    },
  },
  data: () => ({
    items: [],
  }),
  methods: {
    ...mapMutations(["SET_ROLE"]),
    saveRole(tag, fullName) {
      this.SET_ROLE(tag);
      this.navigate(tag, fullName);
    },
    navigate(view, repoFullName) {
      this.$router.push({
        path: `${repoFullName}/pulls`,
      });
    },
  },
};
</script>
