<template>
  <div>
    <Appbar />
    <v-main style="background-color: #0d1117">
      <v-container style="text-align: left">
        <h4 class="repo-list-title">Repositories</h4>
        <SearchBar @search="search" placeholder="Find a repository…" />
        <v-list v-bind:color="color" v-if="repos.length > 0" three-line class="repo-list">
          <template v-for="(item, index) in repos">
            <RepositoryItem :item="item" :key="index" />
          </template>
        </v-list>
        <div v-else class="empty-list"><p class="empty-list-msg">No matching repository found :(</p></div>
        <Pagination class="mt-2" @input="changePage" :length="length" ref="pagination" />
      </v-container>
    </v-main>
  </div>
</template>

<script>
import RepositoryItem from "@/components/RepositoryItem.vue";
import Appbar from "../components/Appbar.vue";
import SearchBar from "../components/SearchBar.vue";
import Pagination from "@/components/Pagination";
import { mapGetters, mapActions, mapMutations } from "vuex";

export default {
  name: "RepositoryList",
  data: () => ({
    color: "#0d1117",
  }),
  methods: {
    ...mapActions(["getUserRepositories", "getPaginatedRepos"]),
    ...mapMutations(["SET_REPO_SEARCH_TERM"]),
    search(searchTerm) {
      this.SET_REPO_SEARCH_TERM(searchTerm);
      this.getPaginatedRepos();
      this.$refs.pagination.reset();
    },
    changePage(pageNo) {
      this.getPaginatedRepos(pageNo);
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },
  },
  beforeDestroy() {
    this.SET_REPO_SEARCH_TERM("");
  },
  computed: {
    ...mapGetters({
      token: "token",
      repos: "paginatedRepos",
      items: "repos",
      paginationLength: "paginationLength",
    }),
    length() {
      return Math.ceil(this.items.length / this.paginationLength);
    },
  },
  async mounted() {
    this.SET_REPO_SEARCH_TERM("");
    await this.getUserRepositories();
    this.getPaginatedRepos();
  },
  components: {
    RepositoryItem,
    Appbar,
    SearchBar,
    Pagination,
  },
};
</script>
